.accordion{width:100%;width:-moz-max-content;width:max-content;margin:0 auto;font-family:"Purple Smile",sans-serif}.accordion-item{background-color:#3626a6;color:#fff;border-radius:1.9375rem;margin:1.25rem 0;overflow:hidden}.accordion-title{display:flex;justify-content:space-between;align-items:center;width:36.8444rem;height:6.6875rem;padding:0 2.375rem;border-radius:1.9375rem;background:#3626a6;border:none;outline:none;color:#ddd8ff;font-family:BoldenVan;font-size:1.875rem;font-weight:400;line-height:1.875rem;letter-spacing:0%;text-align:left;cursor:pointer;transition:background-color .3s}.accordion-title span{width:26.3656rem}.accordion-title .accordion-button{position:relative;display:flex;justify-content:center;align-items:center;width:2.8125rem;height:2.8125rem}.accordion-title .accordion-button .obj{position:absolute;top:-0.125rem}.accordion-title .accordion-button .obj1{rotate:90deg;transform:translate(0.525rem, -0.525rem);transition:all .3s ease-in-out}.accordion-title .accordion-button.active .obj1{rotate:0deg;transform:translate(0.025rem, -0.025rem)}.accordion-content-wrapper{overflow:hidden;max-height:0;width:36.8444rem;transition:max-height .5s ease-in-out}.accordion-content{padding:1.5625rem 7.5956rem 2.8125rem 2.375rem;line-height:1.5;background-color:#3626a6;color:#998aff;font-family:FUNGIS;font-size:1.375rem;font-weight:700;line-height:2rem;letter-spacing:0%;text-align:left}