.animated-link{display:inline-block;text-decoration:none}.animated-link span{display:inline-block;transition:all .3s ease;transform-origin:bottom;font-size:1rem}.animated-link:hover span{animation:jump .4s ease forwards;color:#ffd900}@keyframes jump{0%{transform:translateY(0)}50%{transform:translateY(-0.25rem)}100%{transform:translateY(0)}}